import './App.scss';
import {BrowserRouter, Route, Switch, Redirect} from "react-router-dom";
import Main from "./components/Main";
import Card from "./components/Card";

function App() {
  return (
      <BrowserRouter>
        <div className="wrapper clear">
            <Switch>
                <Route exact path="/react-get-git-repos" component={Main}/>
                <Route path="/react-get-git-repos/card/:username/:repoName" component={Card}/>
                <Redirect to="/react-get-git-repos" />
            </Switch>
        </div>
      </BrowserRouter>
  );
}

export default App;
